html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  font-family: "Hubballi", cursive;
  color: rgb(17, 16, 16);
}

*, *:before, *:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
  color: rgb(22, 21, 21);
}

img {
  padding: 0;
  width: 100%;
}

.contenedor {
  max-width: 115rem;
  width: 100%;
  margin: 0 auto;
}

.barra-desk {
  padding: 2rem 0;
  display: flex;
  justify-content: space-between;
}
.barra-desk .iconos-nav {
  width: 15rem;
  display: flex;
  flex-direction: row;
}
.barra-desk .iconos-nav img {
  object-fit: cover;
  width: 100%;
}

.navegacion-desk {
  display: none;
}

.menu-contenedor {
  display: flex;
  align-items: center;
  padding: 2rem;
}

@media (min-width: 700px) {
  .menu-contenedor {
    display: none;
  }

  .contenedor-nav-desk {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .navegacion-desk {
    display: flex;
    gap: 3rem;
  }
  .navegacion-desk a {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .navegacion-desk img {
    width: 2rem;
  }
  .navegacion-desk p {
    font-size: 1.3rem;
    margin-top: 0;
  }

  .barra-desk {
    padding: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .iconos-nav {
    display: flex;
    margin-bottom: 1.5rem;
  }
}
@media (min-width: 1024px) {
  .barra-desk {
    padding: 2rem 0;
    flex-direction: row;
    justify-content: space-between;
    align-items: inherit;
  }

  .iconos-nav {
    margin-bottom: 0;
  }
}
.contenido-header {
  background-image: url(/build/img/grow-g96dc01bff_1920.webp);
  background-size: cover;
  position: relative;
  background-repeat: no-repeat;
  background-position: center center;
  width: auto;
  height: 50rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.contenido-header .letter-title {
  color: white;
  font-size: 5rem;
  text-transform: uppercase;
  z-index: 5;
  max-width: 40rem;
  font-weight: normal;
}

.contenido-header::before {
  background-color: rgba(14, 13, 13, 0.651);
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 3;
  content: "";
}

.navegacion-mobile {
  position: fixed;
  top: 0;
  background-color: rgb(239, 247, 238);
  height: 100vh;
  padding: 3rem;
  z-index: 7;
  visibility: hidden;
  opacity: 0;
  transition: 0.3s all ease-in-out;
  box-shadow: -10px 0 140px rgb(31, 29, 29);
  display: flex;
  justify-content: center;
  align-items: center;
}

.show-navMobile {
  visibility: visible;
  opacity: 1;
}

.icono-light {
  position: absolute;
  top: 50px;
  right: 100px;
  width: 4rem;
}

.squine {
  position: fixed;
  top: 5px;
  left: 160px;
  z-index: 9;
  width: 4rem;
}

.iconos-link-mobile .iconos-link a {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.iconos-link-mobile .iconos-link img {
  width: 3rem;
}
.iconos-link-mobile .iconos-link p {
  font-size: 1.5rem;
  font-weight: bold;
  font-style: normal;
}

@media (min-width: 700px) {
  .navegacion-mobile {
    display: none;
  }
}
@media (min-width: 700px) and (min-width: 700px) {
  .icono-light {
    width: 3rem;
    margin: 0 2rem;
    position: inherit;
  }
}
.mod-dark-add {
  transition: 0.3s all ease-in-out;
  background-color: rgb(20, 20, 20);
}
.mod-dark-add .navegacion-mobile img, .mod-dark-add .iconos-link img, .mod-dark-add .icono-light img, .mod-dark-add .menu-contenedor img {
  filter: invert(100%);
}
.mod-dark-add .navegacion-mobile p, .mod-dark-add .iconos-link p, .mod-dark-add .icono-light p, .mod-dark-add .menu-contenedor p {
  color: rgb(235, 227, 227);
}
.mod-dark-add .navegacion-mobile {
  background-color: rgb(43, 38, 38);
}
.mod-dark-add .information-main p, .mod-dark-add .information-main h2, .mod-dark-add .contenido-alumnos p, .mod-dark-add .contenido-alumnos h2 {
  color: white;
}

.information-main .title-section {
  text-align: center;
  font-size: 3rem;
  text-transform: uppercase;
}

.informacion-alumnos, .contain-certifaid {
  margin-top: 4rem;
}
.informacion-alumnos p, .contain-certifaid p {
  text-align: justify;
  font-size: 2.5rem;
  padding: 2rem;
}

.contenido-alumnos {
  display: flex;
  gap: 3rem;
  flex-direction: row;
  flex-flow: wrap;
  padding: 2rem;
}
.contenido-alumnos div {
  transition: 0.3s;
  width: 20%;
  margin: 0 auto;
}
.contenido-alumnos div h2 {
  text-align: center;
}
.contenido-alumnos div:hover {
  transform: scale(1.05);
  box-shadow: 0 0 40px -10px rgba(0, 0, 0, 0.25);
}

.body {
  position: relative;
}

.contain-certifaid .contenedor-hijo {
  padding: 4rem 3rem;
}
.contain-certifaid .contenedor-hijo img {
  margin-bottom: 2rem;
}
@media (min-width: 700px) {
  .contain-certifaid .contenedor-hijo {
    padding: 4rem 0;
    display: flex;
    justify-content: space-around;
  }
  .contain-certifaid .contenedor-hijo img {
    width: 40rem;
    transition: 0.3s;
  }
  .contain-certifaid .contenedor-hijo img:hover {
    transform: scale(1.05);
  }
}